#event-logger {
  display: flex;
  flex-direction: column;
  border: solid 1px #eaeaea;
  border-radius: 5px;
  padding: 10px;
  margin: 10px;
  width: 600px;
  height: 400px;
  overflow: scroll; }

@keyframes fadein {
  from {
    margin-top: -5px;
    opacity: 0; }
  to {
    margin-top: 5px;
    opacity: 1; } }

.event {
  margin: 5px 0px;
  padding: 5px;
  border-radius: 3px;
  background: #fafafa;
  color: #777;
  font-size: 10pt;
  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 20px;
  animation-duration: 0.5s;
  animation-name: fadein; }
  .event.open {
    background: #d5ff72;
    color: #5f9225; }
    .event.open .close-button {
      color: #d5ff72;
      background: #5f9225; }
  .event.message {
    background: #d2feff;
    color: #419fca; }
    .event.message .close-button {
      color: #d2feff;
      background: #419fca; }
  .event.error {
    background: #ffea72;
    color: #837c41; }
    .event.error .close-button {
      color: #ffea72;
      background: #837c41; }
  .event.close {
    background: #ffd2d2;
    color: #b14949; }
    .event.close .close-button {
      color: #ffd2d2;
      background: #b14949; }
  .event .status {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 6pt;
    text-transform: uppercase;
    width: 60px; }
  .event .date {
    display: flex;
    font-size: 10pt;
    width: 90px;
    padding-left: 10px; }
  .event .info {
    font-size: 10pt;
    display: flex;
    width: 410px; }
  .event .close-button {
    width: 15px;
    height: 15px;
    border-radius: 8px;
    font-size: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer; }
